import { mapState } from 'vuex'
import {
    getArticles,
    getFeedArticles,
    addFavorite,
    deleteFavorite
} from '@/api/article'
import { getTags } from '@/api/tags'

export default {
    name: 'HomeIndex',
    watchQuery: ['page', 'tag', 'tab'],
    async asyncData({ query, store }) {
        const page = Number.parseInt(query.page || 1)
        const limit = 10
        const { tag } = query
        const tab = query.tab || 'Global_Feed'
        const loadArticle = store.state.user && tab === 'Your_Feed' ? getFeedArticles : getArticles
        const [ articleRes, tagRes ] = await Promise.all([
            loadArticle({
                limit,
                offset: (page - 1) * limit,
                tag
            }),
            getTags()
        ])
        const { articles, articlesCount } = articleRes.data
        const { tags } = tagRes.data

        articles.forEach(item => item.favoriteDisabled = false)

        return {
            articles,
            articlesCount,
            limit,
            page,
            tags,
            tag,
            tab
        }
    },
    computed: {
        ...mapState(['user']),
        totalPage () {
            return Math.ceil(this.articlesCount / this.limit)
        }
    },
    data() {
        return {
        
        }
    },
    methods: {
        async onFavorite(article) {
            article.favoriteDisabled = true
            if (article.favorited) {
                const res = await deleteFavorite(article.slug)
                article.favorited = false
                article.favoritesCount -= 1
            } else {
                const res = await addFavorite(article.slug)
                article.favorited = true
                article.favoritesCount += 1
            }
            article.favoriteDisabled = false
        },
    },
}
